import React, { useState, useEffect } from "react";
import "./registration-page.scss";
import { Tab, Tabs, Grid, Select, MenuItem, FormControl } from "@mui/material";
import citiesService from "../../services/citiesService";

export const RegistrationPage = () => {
  const [value, setValue] = useState(0);
  const [cities, setCities] = useState([]);
  const [city, setCity] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const [typeBiz, setTypeBiz] = useState("");

  const handleChangeTypeBiz = (event) => {
    setTypeBiz(event.target.value);
  };

  const handleCityChange = (event) => {
    setCity(event.target.value);
  };

  useEffect(() => {
    citiesService.getAllCities().then((response) => {
      setCities(response);
    });
  }, []);

  return (
    <>
      <div className="registration-container">
        <h1>Заявка на Регистрацию</h1>
        <Tabs value={value} onChange={handleChange}>
          <Tab label="Магазин" />
          <Tab label="Поставщик" />
        </Tabs>
        <div className="tabs-inside">
          {value === 0 && (
            <Grid>
              <Grid className="rows">
                <label htmlFor="">Наименование:</label>
                <Grid className="input-blocks">
                  <FormControl
                    sx={{
                      minWidth: 120,
                      borderRadius: "20px",
                      marginRight: "20px",
                      "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                        borderColor: "red",
                      },
                    }}
                  >
                    <Select
                      value={typeBiz}
                      onChange={handleChangeTypeBiz}
                      displayEmpty
                      sx={{
                        borderRadius: "20px",
                        height: "40px",
                        border: "2px solid #FCA522",
                        outline: "none",
                      }}
                    >
                      <MenuItem value={10}>ИП</MenuItem>
                      <MenuItem value={20}>ТОО</MenuItem>
                    </Select>
                  </FormControl>
                  <input type="text" />
                </Grid>
              </Grid>
              <Grid className="rows">
                <label htmlFor="">ИИН/БИН:</label>
                <Grid className="input-blocks">
                  <input type="number" />
                </Grid>
              </Grid>
              <Grid className="rows">
                <label htmlFor="">Email:</label>
                <Grid className="input-blocks">
                  <input type="email" />
                </Grid>
              </Grid>
              <Grid className="rows">
                <label htmlFor="">ФИО Оператора:</label>
                <Grid className="input-blocks">
                  <input type="text" />
                </Grid>
              </Grid>
              <Grid className="rows">
                <label htmlFor="">Мобильный телефон:</label>
                <Grid className="input-blocks">
                  <input type="tel" />
                </Grid>
              </Grid>
              <Grid className="rows">
                <label htmlFor="">Рабочий телефон:</label>
                <Grid className="input-blocks">
                  <input type="tel" />
                </Grid>
              </Grid>
              <Grid className="rows">
                <label htmlFor="">Город:</label>
                <Grid className="input-blocks">
                  <FormControl
                    sx={{
                      minWidth: 120,
                      borderRadius: "20px",
                      marginRight: "20px",
                    }}
                  >
                    <Select
                      value={city}
                      onChange={handleCityChange}
                      displayEmpty
                      sx={{
                        borderRadius: "20px",
                        height: "40px",
                        border: "2px solid #FCA522",
                        outline: "none",
                      }}
                    >
                      {cities.map((city) => (
                        <MenuItem value={city.id}>{city.name}</MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>
              </Grid>
              <Grid className="rows">
                <label htmlFor="">Адрес:</label>
                <Grid className="input-blocks">
                  <input type="text" style={{ width: "400px" }} />
                </Grid>
              </Grid>
              <Grid className="rows">
                <label htmlFor="">Лицензия на ИП/ТОО (опционально):</label>
                <Grid className="input-blocks">
                  <input
                    className="file-input"
                    type="file"
                    style={{ width: "400px" }}
                  />
                </Grid>
              </Grid>
              <Grid className="rows">
                <label htmlFor="">Лицензия на алкоголь (опционально):</label>
                <Grid className="input-blocks">
                  <input
                    className="file-input"
                    type="file"
                    style={{ width: "400px" }}
                  />
                </Grid>
              </Grid>
              <Grid className="rows">
                <label htmlFor="">Комментарий:</label>
                <Grid className="input-blocks">
                  <textarea />
                </Grid>
              </Grid>
            </Grid>
          )}
          {value === 1 && (
            <Grid>
              <Grid>
                <label htmlFor="">Наименование</label>
              </Grid>
            </Grid>
          )}
        </div>
      </div>
    </>
  );
};
