import { Footer } from "./common/footer/Footer";
import { Header } from "./common/header/Header";
import { Main } from "./pages/main/Main";
import { LoginPage } from "./pages/LoginPage/LoginPage";
import { RegistrationPage } from "./pages/RegistrationPage/RegistrationPage";
import { Goods } from "./pages/Goods/Goods";
import { Company } from "./pages/Company/Company";
import { Basket } from "./pages/Basket/Basket";
import { NotFoundPage } from "./pages/NotFoundPage/NotFoundPage";
import { Routes, Route } from "react-router";
import { AboutGood } from "./pages/AboutGood/AboutGood";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Providers } from "./pages/Providers/Providers";
import { AboutToProviders } from "./pages/about/aboutPages/AboutToProviders";
import { AboutToCustomers } from "./pages/about/aboutPages/AboutToCustomers";

function App() {
  return (
    <>
      <ToastContainer />
      <Header />
      <Routes>
        <Route path="/" element={<Main />} />
        <Route path="/login" element={<LoginPage />} />
        <Route path="/registration" element={<RegistrationPage />} />
        <Route path="/category/:catId" element={<Goods />} />
        <Route path="/company-types" element={<Company categoryTitle />} />
        <Route path="/company-types/:typeId" element={<Providers />} />
        <Route path="/basket" element={<Basket />} />
        <Route path="/category/:catId/good/:goodId" element={<AboutGood />} />
        <Route path="*" element={<NotFoundPage />} />
        <Route path="/to-providers" element={<AboutToProviders />} />
        <Route path="/to-customers" element={<AboutToCustomers />} />
      </Routes>
      <Footer />
    </>
  );
}

export default App;
