import React, { useEffect, useState } from 'react'
import './providers.scss'
import { useParams,Link } from "react-router-dom";
import goodsService from "../../services/goodsService";
import { ContentHeader } from "../../common/Content/ContentHeader/ContentHeader";
import { ContentScheme } from "../../common/helpers/contentScheme/ContentScheme";
import { HeadMenu } from "../../common/helpers/HeadMenu/HeadMenu";
import { useLocation } from "react-router-dom";
import {
  Breadcrumbs,
  Typography,
  Grid,
  Select,
  FormControl,
  MenuItem,
  InputLabel,
  ButtonGroup,
  Button,
  Pagination,
} from "@mui/material";

export const Providers = (props) => {

  const [providers, setProviders] = useState();
  const [category, setCategory] = useState({
    id:'',
    title: '',
    img: ''
  });
  let { typeId } = useParams();

  const location = useLocation();

  useEffect(()=>{
    goodsService.getProvidersByType({ typeId }).then((response) => {
      setProviders(response);
    });
    setCategory(location.state.category)
  }, [typeId, location]);

  const sidebarContent = () => {
    return <h3>Категории</h3>;
  };

  return (
    <>
      <ContentHeader />
      <HeadMenu />
      <ContentScheme sidebar={sidebarContent()}>
          <h1>{category.title}</h1>
          <Breadcrumbs
              className="breadcrumbs"
              aria-label="breadcrumb"
              sx={{ color: "rgba(0,0,0,.20)" }}
            >
          <Link underline="hover" color="inherit" to="/">
            Главная
          </Link>
          <Link underline="hover" color="inherit" to="/company-types">
            Фирмы
          </Link>
          <Typography color="text.primary">{category.title}</Typography>
        </Breadcrumbs>

      </ContentScheme>
    </>
  )
}
